/**=====================
    3.37 Social-app CSS start
==========================**/
.user-profile {
  .hovercard {
    .socialheader {
      background: url(../../images/social-app/social-image.png);
    }

    .user-image {
      .share-icons {
        position: absolute;
        right: 100px;
        top: -27px;

        li {
          display: inline-block;
          margin-right: 10px;
          padding: 0px;

          .social-icon {
            width: 50px;
            height: 50px;
            text-align: center;
            display: flex;
            align-items: center;
            vertical-align: middle;
            justify-content: center;
            border-radius: 100%;

            i {
              color: $white;
              font-size: 25px;
            }
          }

          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }

  .card-footer {
    >div {
      h3 {
        font-size: 16px;
      }
    }
  }
}

.market-tabs {
  .border-tab.nav-tabs .nav-item .nav-link.active {
    border-bottom: none !important;
  }

  .nav {
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;

    .nav-item {
      width: 100%;

      .nav-link {
        height: 81px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        padding-top: 30px;
        text-align: center;
      }
    }
  }
}

.tabs-scoial {
  border-bottom: none;
  margin-bottom: 20px !important;
  margin-top: 20px;

  li {
    a {
      font-weight: 600;

      svg {
        circle {
          color: $light-text;
        }
      }
    }
  }
}

.social-network {
  span {
    margin-bottom: 30px;
    display: block;
  }
}

.social-list {
  .d-flex {
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    &:last-child {
      margin-bottom: 0;
    }

    .flex-grow-1 {
      a {
        color: $light-text;
      }

      span {
        font-weight: 600;
      }

      &:hover {
        a {
          color: var(--theme-default);
        }
      }
    }
  }
}

.photos {
  margin-bottom: -15px;

  ul {
    li {
      display: inline-block;
      margin-right: 10px;
      margin-bottom: 10px;
      width: 70px;

      &:nth-child(4n) {
        margin-right: 0;
      }
    }
  }
}

.new-users-social {
  margin-bottom: 30px;

  svg {
    cursor: pointer;
  }

  .d-flex {
    img {
      width: 58px;
    }

    .flex-grow-1 {
      margin-top: 10px;
    }
  }

  svg {
    circle {
      color: $light-text;
    }
  }
}

.like-content {
  span {
    i {
      font-size: 14px;
    }
  }
}

.timeline-content {
  p {
    margin-top: 30px;
    color: $light-text;
  }

  .comment-number {
    i {
      color: $light-text;
      margin-right: 20px;
    }
  }

  .comments-box {
    margin-top: 30px;
    .flex-grow-1{
      width: 100%;
    }
    .input-group {
      margin-top: 6px;
      .input-group-append{
        background-color: $body-color;
        border-top-right-radius: 4px !important;
        border-bottom-right-radius: 4px !important;
        &:focus{
          box-shadow: none;
        }
      }

      .input-txt-bx {
        border: none;
        background-color: $main-body-color;

        &::placeholder {
          color: $light-text;
        }
      }

      .input-group-append {
        .btn {
          i {
            font-size: 20px;
            display: flex;
            color: $light-text;
          }
        }
      }
    }
  }
}

.social-chat {
  margin-top: 30px;

  span {
    span {
      font-weight: normal;
      color: $light-text;
      font-size: 12px;
    }
  }

  .flex-grow-1 {
    padding: 20px;
    border: 1px solid $light-color;
    position: relative;

    p {
      margin-top: 5px;
    }

    &:before {
      content: '';
      position: absolute;
      top: 19px;
      right: 100%;
      border: 7px solid transparent;
      border-right: 7px solid $semi-dark;
    }

    &:after {
      content: '';
      position: absolute;
      top: 19px;
      right: 100%;
      border: 7px solid transparent;
      border-right: 7px solid $white;
    }
  }

  .your-msg,
  .other-msg {
    margin-bottom: 30px;
  }

  .other-msg {
    margin-left: 40px;
  }
}


.socialprofile {
  span {
    color: $light-text;
    &.badge{
      padding: 6px 10px !important;
    }
  }
 
  .social-btngroup {
    margin: 30px 0;
  }

  .likes-profile {
    h5 {
      span {
        color: $theme-body-font-color;
      }
    }
  }

  .social-group {
    margin-top: 30px;

    ul {
      li {
        padding: 0 !important;
        &:nth-child(4) {
          margin-left: -22px;
        }
      }
    }
  }
}
.social-app-profile{
  .collapse{
    .photos{
      ul{
        display: block;
        li{
          padding: 0;
          img {
            height: unset;
            width: unset !important;
          }
        }
      }
    }
  }
}
.social-status {
  form {
    .form-group {
      .form-control-social {
        border: 1px solid $light-color;
        padding-left: 15px;
        margin-bottom: 30px;
      }
    }
  }

  .d-flex {
    position: relative;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }

    .social-status {
      content: '';
      position: absolute;
      width: 12px;
      height: 12px;
      background-color: #fff;
      bottom: 0;
      left: 35px;
      border-radius: 100%;
    }

    .social-online {
      border: 3px solid $success-color;
    }

    .social-busy {
      border: 3px solid $warning-color;
    }

    .social-offline {
      border: 3px solid $semi-dark;
    }

    .flex-grow-1 {
      p {
        color: $light-text;
        margin-bottom: 5px;
      }

      span {
        +span {
          color: $light-text;
        }
      }

      .light-span {
        color: $light-text;
      }
    }
  }
}

.social-header {
  h5 {
    span {
      &:first-child {
        line-height: 1.48;
      }

      svg {
        circle {
          color: $light-text;
        }
      }
    }
  }
}

.details-about {
  +.details-about {
    margin-top: 30px;
  }

  .your-details {
    p {
      color: $light-text;
    }
  }
}

.avatar-showcase {
  .friend-pic {
    margin-right: 8px;
    margin-bottom: 10px;
  }

  .pepole-knows {
    ul {
      li {
        display: inline-block;
        margin-right: 20px;
        margin-bottom: 10px;
        padding: 0px; 

        &:last-child {
          margin-right: 0;
        }

        .add-friend {
          img{
            margin: 0 auto;
            height: 100%;
          }
          h6 {
            margin-top: 3px;
          }
        }
      }
    }
  }
}

.activity-log {
  .my-activity {
    +.my-activity {
      margin-top: 30px;
    }

    p {
      margin-bottom: 5px;

      span {
        svg {
          width: 15px;
          height: 15px;
        }
      }
    }
  }
}

/**=====================
    3.37 Social-app CSS end
==========================**/