/**=====================
  3.13 Dashboard_default CSS Start
==========================**/
//index dashboard css start

  .form-control-plaintext {
    width: auto !important;
    box-shadow: unset;
    &:focus,
    &:focus-visible {
      outline: none;
      box-shadow: unset;
      border-radius: 0;
      border: none;
    }
    outline: none;
  }
  
  @keyframes bounce-effect {
    0% {
      transform: translateY(0);
      transition: 0.5s;
    }
  
    50% {
      transform: translateY(-5px);
      transition: 0.5s;
    }
  
    100% {
      transform: translateY(0);
      transition: 0.5s;
    }
  }
  
  @keyframes round {
    0% {
      transform: scale(1);
    }
  
    50% {
      transform: scale(1.2);
    }
  
    100% {
      transform: scale(1);
    }
  }
  //badge 
  @each $badge-light-name,
  $badge-light-color in (primary, $primary-color),
  (secondary, $secondary-color),
  (success, $success-color),
  (danger, $danger-color),
  (info, $info-color),
  (light, $badge-light-color),
  (dark, $dark-color),
  (warning, $warning-color) {
    .badge-light-#{$badge-light-name} {
      background-color: lighten($badge-light-color, 38%);
      span{
        color: $badge-light-color !important;
      }
    }
  }
  
  .badge-light-light {
    background-color: lighten($badge-light-color, 55%);
  }
  
  .badge-light-primary { 
    background-color: rgba($primary-color, 0.1);
    &:hover{
      color: var(--theme-default);
      background-color: rgba($primary-color, 0.1);
    }
  }
  
  .badge-light-success {
    background-color: rgba($success-color, 0.15);
  }
  
  .badge-light-danger {
    background-color: rgba($danger-color, 0.3);
  }
  
  .badge-light-info{
    background-color: rgba($info-color, 0.3);
  } 
  
  .badge-light-secondary {
    background-color: rgba($secondary-color, 0.08);
  }
  
  // select2
  .badge-light-warning {
    background-color: rgba($warning-color, 0.1);
  }
  
  .table {
    thead {
      th {
        border-bottom: 2px solid $light-gray;
      }
    }
  }
  
  .overall-card {
    .balance-card {
      @media (max-width:1712px) {
        padding: 12px;
      }
    }
  
    @media (max-width: 1662px) and (min-width:1600px) {
      .col-xl-9 {
        width: 73%;
      }
  
      .col-xl-3 {
        width: 27%;
      }
    }
  
    @media (max-width: 1650px) and (min-width:1600px) {
      .col-xl-9 {
        width: 72%;
      }
  
      .col-xl-3 {
        width: 28%;
      }
    }
  
    @media (max-width: 1600px) and (min-width:1500px) {
      .col-xl-9 {
        width: 70%;
      }
  
      .col-xl-3 {
        width: 30%;
      }
    }
  
    @media (max-width: 1500px) and (min-width:1200px) {
      .col-xl-9 {
        width: 65%;
      }
  
      .col-xl-3 {
        width: 35%;
      }
    }
  }
  
  /* Clock styles */
  .clockbox {
    margin-bottom: -5px;
  
    svg {
      width: 45px;
      height: 45px;
  
      @media (max-width: 767px) {
        height: 36px;
      }
    }
  }
  

  
  .circle {
    fill: $white;
    stroke: rgba($white, 0.4);
    stroke-width: 80px;
    stroke-miterlimit: 10;
  }
  
  .mid-circle {
    fill: #000;
  }
  
  .hour-marks {
    fill: none;
    stroke: $theme-body-sub-title-color;
    stroke-width: 9;
    stroke-miterlimit: 10;
  }
  
  .hour-hand {
    fill: none;
    stroke: #ff7e00;
    stroke-width: 17;
    stroke-miterlimit: 10;
  }
  
  .minute-hand {
    fill: none;
    stroke: #ff7e00;
    stroke-width: 11;
    stroke-miterlimit: 10;
  }
  
  .second-hand {
    fill: none;
    stroke: $warning-color;
    stroke-width: 15px;
    stroke-miterlimit: 10;
  }
  
  .sizing-box {
    fill: none;
  }
  
  #hour,
  #minute,
  #second {
    transform-origin: 300px 300px;
    transition: transform .5s ease-in-out;
  }
  
  .widget-grid {
    .height-equal {
      @media (min-width: 1400px) {
        min-height: unset !important;
      }
    }
  
    >.col-xxl-auto {
      @media (min-width: 1400px) {
        width: 22.22%;
      }
    }
  }
  
  .order-chart {
    >div {
      margin-top: -25px;
      margin-bottom: -43px;
    }
  
    svg {
      path {
        clip-path: inset(7% 0% 0% 0% round 0.6rem);
      }
    }
  }
  
  .growth-wrap {
    .card-header {
      position: relative;
      z-index: 1;
    }
  }
  
  .growth-wrapper {
    >div {
      margin-top: -54px;
      margin-bottom: -25px;
  
      @media (max-width: 1481px) {
        margin-top: -50px;
      }
    }
  }
  
  .profit-chart {
    >div {
      margin-top: -45px;
      margin-bottom: -48px;
    }
  
    .apexcharts-canvas {
      .apexcharts-tooltip-text-label {
        display: none;
      }
    }
  }
  // new admin css start 
  .card{
    .card-footer{
      .common-space{
        a{
          color: $theme-body-font-color;
        }
        &:hover{
          a{
            color: $primary-color;
          }
        }
      }
    }
  }
  .sales-wrapper{
    .apexcharts-xaxistooltip{
      display: none;
    }
  }
  .total-order{ 
    .apexcharts-xaxistooltip{
      display: none;
    }
  }
  .sales-chart{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .sales-chart-dropdown{
    display: flex;
    gap: 15px;
    align-items: center;
    .sales-chart-dropdown-select{
      display: flex;
      justify-content: flex-end;
      gap: 15px;
    }
    .balance-data{
      .list-group-item{
        padding: 0;
      }
    }
  }
  .simple-list{
    li
    {
      &.sale-history-card{
        padding: 0;
        border: none;
        border-bottom: 1px solid #E6E9EB;
        &:last-child{
          border: none;
        }
      }
    }
  }
  .total-revenue{
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    span{
      font-weight: 500;
    }
    .update-data{
      position: relative;
      &::before{
        content: "";
        position: absolute;
        height: 4px;
        width: 4px; 
        bottom: 3px;
        left: -15px;
        bottom: 9px;
        border-radius: 15px;
        background-color: $light-txt-color;
      }
    }
    a{
      font-weight: 600;
    }
  }
  .total-chart{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: -45px;
    margin-top: -20px;
    .data-grow{
      svg{ 
        width: 24px;
        height: 24px;
        cursor: pointer;
      }
      span{ 
        color: $theme-body-font-color;
      }
    }
  }
  .special-Offer{
    background-image: url(../../images/dashboard/Banner.png);
    background-size: cover;
    background-repeat: no-repeat;
    height: 363px;
    border-radius: 8px;
    width: 100%; 
    background-position: center;
    h4{
      font-weight: 700;
      font-size: 22px;
      color: $white;
      text-align: center;
    }
    p{
      font-size: 12px;
      font-weight: 500; 
      color: $white;
      display: flex;
      align-items: center;
      justify-content: center;
    } 
    .offer-contain{
      padding-top: 45px;
      text-align: center;
      p{
        margin: 0px 20px ;
      }
    }
  }
  
  .ecommerce-banner{
    background-image: url(../../images/dashboard/Dashboard-Ecommerce-banner.gif);
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%; 
    height: 190px;
  }
  .product-widget{
    &:hover{ 
      transform: translateY(-5px);
      transition: 0.5s;
    }
  }
  .new-product{
    padding: 16px !important;
    svg{
      cursor: pointer;
    }
    &:hover{
      .product-cost{
        .add-product{
          svg{ 
            animation: tada 1.5s ease infinite;
          }
        }
      }
    }
    .product-icon{
      height: 50px;
      width: 50px;
      border-radius: 6px;
    }
    .product-cost{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .add-product{
        display: flex;
        align-items: center;
        gap: 16px;
        h6 {
          display:-webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        span{
          display:-webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden; 
        }
      }
      .product-icon{
        svg{ 
          height: 25px; 
          margin: 12px;
          width: 25px;
        }
      }
    }
  }
  
  .product-costing{
    margin-bottom: 14px;
    border-bottom: 1px dashed $light-color;
    li{
      &.list-group-item{
        border: none;
        padding: 0;   
      }
    }
    p{ 
      font-size: 14px;
      font-weight: 500;
    }
    .list-group-item{
      padding: 0;
      border: none;
    }
    .product-cost{
      &.list-group-item{
        padding: 0;
        border: none;
      }
    }
    span{
      margin-bottom: 14px;
      display:-webkit-box;
      -webkit-line-clamp: 1; 
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    &:last-child{
      span{
        margin-bottom: 0px; 
      }
      border: none;
      margin-bottom: 0px;
    } 
    .product-icon{
      height: 50px;
      width: 50px;
      border-radius: 6px;
    }
    &:last-child{
      .costing-span{
        margin-bottom: 0px;
      }
    }
    .product-cost{
      display: flex;
      gap: 15px;
      margin-bottom: 8px;
      padding: 0;
      border: none;
      .product-icon{
        svg{
          height: 25px;
          margin: 12px;
          width: 25px;
          cursor: pointer;
        }
      }
    }
    &:hover{
      .product-cost{
        svg{
          animation: tada 1.5s ease infinite;
        }
      }  
    }
  }
  @keyframes tada {
    0% {
      transform: scaleX(1);
    }
    10% {
      transform: scale3d(0.9, 0.9, 0.9) rotate(-3deg);
    }
    20% {
      transform: scale3d(0.9, 0.9, 0.9) rotate(-3deg);
    }
    30% {
      transform: scale3d(1.1, 1.1, 1.1) rotate(3deg);
    }
    50% {
      transform: scale3d(1.1, 1.1, 1.1) rotate(3deg);
    }
    70% {
      transform: scale3d(1.1, 1.1, 1.1) rotate(3deg);
    }
    90% {
      transform: scale3d(1.1, 1.1, 1.1) rotate(3deg);
    }
    40% {
      transform: scale3d(1.1, 1.1, 1.1) rotate(-3deg);
    }
    60% {
      transform: scale3d(1.1, 1.1, 1.1) rotate(-3deg);
    }
    80% {
      transform: scale3d(1.1, 1.1, 1.1) rotate(-3deg);
    }
    to {
      transform: scaleX(1);
    }
  }
  @keyframes swing {
    20% {
      transform: rotate(15deg);
    }
    40% {
      transform: rotate(-10deg);
    }
    60% {
      transform: rotate(5deg);
    }
    80% {
      transform: rotate(-5deg); 
    }
    to {
      transform: rotate(0deg);
    }
  }
  .sale-history-card{
    margin-bottom: 16px;
    border-bottom:1px solid $light-color;
    &:last-child{
      margin-bottom: 0px;
      border: none;
    }
    &:hover{
      .history-price{
        a{
          color: $primary-color !important;
        }
      }
    }
    .history-price{
      display: flex;
      align-items: center;
      justify-content: space-between;
      a{ 
        display:-webkit-box;
        -webkit-line-clamp: 1; 
        -webkit-box-orient: vertical; 
        overflow: hidden;
        color: $theme-body-font-color ;
      }
    }
    &:last-child{
      .state-time{
        margin-bottom: 0px;
      }
    }
    .state-time{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 16px;
    }
  }
  .table-order{
    .order-table-images{
      height: 35px;
    }
    .product-name{
      display: flex;
      align-items: center;
      gap: 15px;
      img{
        border-radius: 4px;
      }
    }
    .invoice-icon{
      height: 24px;
      width: 24px;
    }
    .latest-orders{
      thead{
        border: none;
        background-color: rgba($light-color,0.3);
        tr{
          th{ 
            border: none;
            padding: 20px; 
            &:nth-child(1){
              min-width: 190px;
            } 
            &:nth-child(2){ 
              min-width: 188px;
              padding: 20px 0px;
            }
            &:nth-child(3){
              min-width: 130px;
              padding: 20px 0px;
            }
            &:nth-child(4){
              min-width: 90px;
              padding: 20px 0px;
              text-align: center;
            } 
            &:nth-child(5){
              min-width: 50px;
            }
            &:first-child{
              border-top-left-radius: 5px !important;
              border-bottom-left-radius: 5px !important;
            }
            &:last-child{
              border-top-right-radius: 5px !important;
              border-bottom-right-radius: 5px !important;
            }
          }
        }
      }
      tbody{
        tr{
          border-bottom: 1px solid $light-color;
          td{
            &:nth-child(4){
              .badge { 
                padding: 7px 18px;
              }
            } 
            &:last-child {
              text-align: center;
            }
            padding-top: 17px;
            padding-bottom: 17px;
            p { 
              color: var(--body-font-color);
              font-weight: 500; 
            } 
            .status-process {
              border-radius: 15px;
              padding: 12px;
            }
            &:last-child {
              .product-sub {
                text-align: center;
              }
            }
          }
          &:last-child{
            border: none; 
            td{
              padding-bottom: 0px;
              overflow: hidden;
              border-bottom: none;
            } 
          }
        }
        tr{
          td{
            a{ 
              color: $theme-body-font-color;
            }
          }
          &:hover{
            td{ 
              a{
                color: var(--theme-default) !important;
              }
            }
          }
        }
      }
    }
    .order-table{
      thead{ 
        background-color: rgba($light-color,0.3);
        tr{
          th{ 
            border: none;
            padding: 20px;
            &:nth-child(1){ 
              min-width: 190px; 
            } 
            &:nth-child(2){ 
              min-width: 150px;
              padding: 20px 0px;
            }
            &:nth-child(3){
              min-width: 130px;
              padding: 20px 0px;
            }
            &:nth-child(4){
              min-width: 90px;
              padding: 20px 0px;
            } 
            &:nth-child(5){
              min-width: 50px;
            }
            &:first-child{
              border-top-left-radius: 5px !important;
              border-bottom-left-radius: 5px !important;
            }
            &:last-child{
              border-top-right-radius: 5px !important;
              border-bottom-right-radius: 5px !important;
              text-align: center;
            }
          }
        }
      }
      tbody{
        tr{
          border-bottom: 1px solid $light-color;
          td{
            &:nth-child(4){
              .badge {
                padding: 9px 18px; 
              } 
            } 
            &:last-child {
              text-align: center;
            }
            padding-top: 17px;
            padding-bottom: 17px;
            p { 
              color: var(--body-font-color);
              font-weight: 500; 
            } 
            .status-process {
              border-radius: 15px;
              padding: 12px;
            }
            &:last-child {
              .product-sub {
                text-align: center;
              }
            }
          }
          &:last-child{
            border: none; 
            td{
              padding-bottom: 0px;
              border-bottom: none;
            } 
          }
        }
        tr{
          td{
            a{ 
              color: $theme-body-font-color;
            }
          }
          &:hover{
            td{ 
              .product-name{
                .product-sub{
                  a{
                    color: var(--theme-default) !important;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .revenueproductwidget{
    margin: 230px 0px 0px 0px;
  } 
  .revenueproduct{
    .apexcharts-canvas{
      .apexcharts-legend{
        display: flex;
        flex-direction: column;
        gap: 5px;
      }
    }
    .apexcharts-canvas {
      .apexcharts-legend-marker {
        border-radius: 4px !important;
      }
      .apexcharts-legend-text{
        font-size: 14px !important; 
      }
    } 
  }
  .widget-1 {
    background-image: url(../../images/dashboard/widget-bg.png);
    background-size: cover;
    margin-bottom: 25px;
  
    i {
      font-weight: 700;
      font-size: 12px;
    }
  
    .f-w-500 {
      display: flex;
      align-items: center;
  
      @media (max-width: 1580px) and (min-width: 1200px) {
        display: none;
      }
    }
  
    .card-body {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      padding: 18px 25px;
  
      @media (max-width: 1720px) {
        padding: 18px;
      }
    }
  
    .widget-round {
      position: relative;
      display: inline-block;
      border-width: 1px;
      border-style: solid;
      border-radius: 100%;
  
      .bg-round {
        width: 56px;
        height: 56px;
        box-shadow: $widget-shadow;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        margin: 6px;
        position: relative;
        z-index: 1;
  
        svg {
          width: 24px;
          height: 24px;
        }
  
        .half-circle {
          height: 52px;
          position: absolute;
          left: -9px;
          width: 35px;
          bottom: -8px;
          background: $white;
          z-index: -1;
        }
  
        @media (max-width:1600px) {
          width: 40px;
          height: 40px;
  
          svg {
            width: 22px;
            height: 22px;
          }
  
          .half-circle {
            height: 40px;
            left: -10px;
            width: 30px;
            bottom: -8px;
          }
        }
      }
  
      &.primary {
        border-color: var(--theme-default);
      }
  
      &.secondary {
        border-color: var(--theme-secondary);
      }
  
      &.success {
        border-color: $success-color;
      }
  
      &.warning {
        border-color: $warning-color;
      }
    }
  
    .widget-content {
      display: flex;
      align-items: center;
      gap: 15px;
  
      @media (max-width:1600px) {
        gap: 10px;
      }
  
      h4 {
        margin-bottom: 4px;
      }
    }
  
    &:hover {
      transform: translateY(-5px);
      transition: 0.5s;
  
      .widget-round {
        .svg-fill:not(.half-circle) {
          animation: tada 1.5s ease infinite;
        }
      }
    }
  }
  
  .widget-with-chart {
    .card-body {
      align-items: center;
    }
  }
  
  .profile-box {
    background: $gradient-bg-primary;
    color: $white;
  
    .cartoon {
      position: absolute;
      bottom: -15px;
      right: 20px;
      animation: bounce-effect 5s infinite ease-in;
  
      [dir="rtl"] & {
        right: unset !important;
        left: 5px;
      }
  
      @media (max-width: 1660px) {
        right: 30px;
        text-align: right;
  
        [dir="rtl"] & {
          left: -6px;
          text-align: left;
        }
  
        img {
          width: 80%;
        }
      }
  
      @media (max-width: 1500px) {
        img {
          width: 60%;
        }
      }
  
      @media (max-width: 767px) {
        right: 10px;
  
        [dir="rtl"] & {
          left: -10px;
        }
  
        img {
          width: 52%;
        }
      }
  
      @media (max-width: 575px) {
        right: 30px;
  
        [dir="rtl"] & {
          left: 10px;
        }
  
        img {
          width: 48%;
        }
      }
    }
  
    .greeting-user {
      p {
        width: 60%;
  
        @media (max-width: 1500px) {
          width: 70%;
        }
  
        @media (max-width: 1199px) {
          width: 80%;
        }
  
        @media (max-width: 767px) {
          width: 98%;
        }
  
        @media (max-width: 600px) {
          width: 100%;
        }
  
        @media (max-width: 575px) {
          width: 98%;
        }
      }
    }
  
    .whatsnew-btn {
      margin-top: 3.5rem;
  
      @media (max-width: 1500px) {
        margin-top: 1.7rem;
      }
  
      @media (max-width: 991px) {
        margin-top: 3.5rem;
      }
  
      @media (max-width: 638px) {
        margin-top: 2rem;
      }
  
      .btn {
        @media (max-width: 767px) {
          padding: 6px 10px;
        }
      }
    }
  }
  
  .header-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
