  span.twitter-typeahead {
    .league-name {
      font-size: 16px;
      padding: 6px 10px 0;
    }
  
    .tt-menu {
      float: left;
      width: 100%;
      min-width: 10rem;
      margin: 0.125rem 0 0;
      font-size: 1rem;
      color: #212529;
      text-align: left;
      list-style: none;
      background-color: $white;
      background-clip: padding-box;
      border-radius: 0.25rem;
      -webkit-box-shadow: 0 0 20px rgba(89, 102, 122, 0.1);
      box-shadow: 0 0 20px rgba(89, 102, 122, 0.1);
      border: none;
      padding: 0;
    }
  
    .tt-suggestion {
      color: $dark-editor-document;
      opacity: 0.6;
      font-size: 13px;
      padding: 6px 12px;
      border-top: 1px solid $light-semi-gray;
      background: $white;
      cursor: pointer;
  
      &:hover,
      &:focus {
        background-color: $white;
      }
  
      &.active,
      &:active {
        color: $white;
        background-color: var(--theme-default);
      }
    }
  }
  
  #scrollable-dropdown-menu {
    .twitter-typeahead {
      .tt-menu {
