/**=====================
    5.8 Rtl CSS Start
==========================**/
html[dir="rtl"] {
  ul {
    padding-right: 0;
  }

  .ms-auto {
    margin-left: unset !important;
    margin-right: auto !important;
  }
  .revenuegrowth{
    .revenuegrowth-chart{
      &::before{ 
        right: unset;
        left: 0px;
      }
    }
  } 
  .custom-input {
    .invalid-tooltip {
      left: 0;
      right: unset !important;
    }
  }
  
  .img-boostup{
    .img-boostup-img-1{
      left: unset; 
      right: -63px;
      transform: scaleX(-1); 
    }
    .img-boostup-img-2{
      transform: scaleX(-1);
      left: unset; 
      right: -61px; 
    } 
  }

  .table-order, .project-table{
    .order-table{
      thead{ 
        tr{
          th{
            &:first-child{
              border-top-left-radius: unset !important; 
              border-bottom-left-radius: unset !important; 
              border-top-right-radius: 5px  !important;
              border-bottom-right-radius: 5px !important;
            } 
            &:last-child{
              border-top-right-radius: unset !important;
              border-bottom-right-radius: unset !important; 
              border-top-left-radius: 5px !important; 
              border-bottom-left-radius: 5px !important; 
            }
          }
        } 
      } 
    }
  }  
  .tranaction-table{
    thead{ 
      tr{
        th{
          &:first-child{
            border-top-left-radius: unset !important; 
            border-bottom-left-radius: unset !important; 
            border-top-right-radius: 5px  !important;
            border-bottom-right-radius: 5px !important;
          } 
          &:last-child{
            border-top-right-radius: unset !important;
            border-bottom-right-radius: unset !important; 
            border-top-left-radius: 5px !important; 
            border-bottom-left-radius: 5px !important; 
          }
        }
      } 
    }  
  }

  .user-message {
    ul {
      li {
        .activity-log {
          .status {
            right: 25px !important;
          }
        }
      }
    }
  }
  
  .activity-table{
    table {
      tbody {
        tr{
          td{
            .team-activity{
              &::before{
                right: 24px;
                left: unset;
              }
            } 
            .activity-data{
              span{
                &::before{
                  right: -9px;
                  left: unset;
                }
              }
              .common-space{
                &::before{
                  bottom: 0px;
                  top: unset;
                  right: unset;
                  left: 0px;
                }
              }
              .user-activity{
                &::before{
                  bottom: 0px;
                  top: unset;
                  right: unset;
                  left: 0px; 
                }
              }
            }  
          } 
        } 
      }
    }
  }
  .important-project{
    .projectlist-card{
      .range_4{
        .slider-container{
          direction: ltr;
          .slider_input{
            left: 0;
          }
        }
        .rounded-2{
          >div{
            transform: translate(-177px, -7.5px);
          }
        }
      }
    }
  }
  .new-user {
    ul {
      li {
        .user-name {
          .dropdown {
            .dropdown-toggle-store {
              width: 34px;
            }
          }
        }
      }
    }
  }
  .timeline-calendar {
    .react-calendar-timeline {
      .rct-items {
        .rct-item {
          &:first-child {
            right: 535px;
          }
          &:nth-child(2){
            right: 535px;
          }
        }
      }
    }
  }
  .add-project{
    .categories{
      transform: scaleX(-1);
      left: 0;
      right: unset;
    }
  } 
  .activity-group{
    &::before{
      content: "" ;
      right: 0px ;
      left: unset;
    }
  }
  .projectlist-card{
    .project-comment{
      .project-comment-icon{
        .project-link{
          &::before{
            left: 35px;
            right: unset;
          }
        }
      }
    }
  }   
  .figure {
    &.text-start{ 
      blockquote{
        border-left: none !important;
        border-right: $blockquote-border !important;
      }
    } 
    &.text-end {
      blockquote {
        border-right: none !important;
        border-left: $blockquote-border !important; 
      }
    }
  }
  .client-list-table{
    table{
      tbody{
        tr{
          td{
            .user-id{
              &::before{
                right: unset;
                left: 11%;
              }
            }
          }
        } 
      }
    }
  }    
  .project-table {
    .project-comment{
      .project-comment-icon{
        .project-link{
          &::before{
            right: 47px;
            left: unset;
          }
        }
      }
    }
  }   
  .activity-log-card{
    ul{
      &::before{ 
        right: 36px;
        left: unset; 
      }
    }
  }
  .slider-wrapper {
    direction: ltr;
  }

  .f-right {
    float: left;
  }

  .papernote-wrap {
    .note-labels {
      ul {
        gap: 9px;
      }
    }
  }

  .form-check-inline {
    margin-right: unset;
    margin-left: 1rem;
  }

  .knowledgebase-search {
    .form-inline {
      svg {
        left: unset;
        right: 20px;
      }
    }
  }

  .switch-showcase {
    .form-check {
      .form-check-input {
        float: left;
      }
    }
  }
  .horizontal-list-wrapper {

    .list-group-horizontal-sm,
    .list-group-horizontal-md,
    .list-group-horizontal-lg,
    .list-group-horizontal-xl,
    .list-group-horizontal-xxl {
      .list-group-item {
        &:first-child {
          border-bottom-left-radius: 0;
          border-top-right-radius: 6px;
          border-bottom-right-radius: 6px;
          border-top-left-radius: 0;
        }

        &:last-child {
          border-top-right-radius: 0;
          border-bottom-left-radius: 6px;
          border-top-left-radius: 6px;
          border-bottom-right-radius: 0;
          border-left-width: 1px;
        }
      }
    }
  }  
  .pill-input-group span {
    &:last-child {
      border-top-right-radius: unset !important;
      border-bottom-right-radius: unset !important;
      border-top-left-radius: 1.3rem !important;
      border-bottom-left-radius: 1.3rem !important;
    }

    &:first-child {
      border-top-left-radius: unset !important;
      border-top-right-radius: unset !important;
      border-top-right-radius: 1.3rem !important;
      border-bottom-right-radius: 1.3rem !important;
    }
  }

  .page-wrapper {
    &.compact-wrapper {
      .page-body-wrapper {
        .sidebar-wrapper {
          text-align: right;
          border-top-left-radius:15px;
          border-top-right-radius: unset;
          border-bottom-left-radius:15px;
          border-bottom-right-radius: unset;
          .toggle-sidebar {
            right: unset;
            left: 30px;
          }

          .sidebar-main .sidebar-links {
            padding: 0;

            .simplebar-wrapper {
              .simplebar-mask {
                .simplebar-content-wrapper {
                  .simplebar-content {
                    >li .badge {
                      left: 50px !important;
                      right: unset !important;
                    }
                  }
                }
              }
            }
          }

          &.close_icon {
            margin-right: 0;
            margin-left: unset;

            .logo-icon-wrapper {
              text-align: left;

              img {
                margin-left: 8px;
              }
            }

            ~.page-body,
            ~footer {
              margin-left: 0;
              margin-right: 100px;
              transition: 0.5s;
            }

            .sidebar-main {
              .sidebar-links {
                .simplebar-wrapper {
                  .simplebar-mask {
                    .simplebar-content-wrapper {
                      .simplebar-content {
                        >li {
                          a {
                            svg {
                              margin-left: 0;
                            }
                          }
                        }
                      }
                    }
                  }
                }

                .sidebar-list {
                  .sidebar-title {
                    &.active {
                      ~.sidebar-submenu {
                        display: none !important;
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              display: none;
            }

            &:hover {
              .sidebar-main {
                .sidebar-links {
                  .simplebar-wrapper {
                    .simplebar-mask {
                      .simplebar-content-wrapper {
                        .simplebar-content {
                          >li {
                            a {
                              svg {
                                margin-left: 13px;
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          &:hover {
            transform: translate(0px);

            .sidebar-main {
              .sidebar-links {
                .sidebar-list {
                  .sidebar-title {
                    &.active {
                      ~.sidebar-submenu {
                        display: block !important;
                      }
                    }
                  }
                }

                .simplebar-wrapper {
                  .simplebar-mask {
                    .simplebar-content-wrapper {
                      .simplebar-content {
                        >li {
                          a {
                            svg {
                              float: none;
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              display: block;
            }
          }

          .sidebar-main {
            .sidebar-links {
              .simplebar-wrapper {
                .simplebar-mask {
                  .simplebar-content-wrapper {
                    .simplebar-content {
                      >li {
                        .sidebar-submenu {
                          &:after {
                            right: 30px;
                            left: unset;
                            border-top-right-radius: 8px;
                            border-right: 2px solid ;
                          }
                          >li {
                            a {
                              padding-right: 50px !important;
                              padding-left: unset !important;
                              &:before{
                                right: 32px;
                              } 
                              &:after {
                                right: 20px;
                                left: unset; 
                                top: 9%; 
                                border-bottom-left-radius: unset;
                                border-bottom-right-radius: 8px;
                                border-left: unset; 
                                border-right: 2px solid rgba($white,0.5);
                                border-bottom: 2px solid rgba($white,0.5);
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .page-body {
          margin-left: unset;
          margin-right: 265px;
        }

        .sidebar-wrapper {
          .sidebar-main {
            .sidebar-links {
              .simplebar-wrapper {
                .simplebar-mask {
                  .simplebar-content-wrapper {
                    .simplebar-content {
                      >li {
                        a {
                          svg {
                            margin-right: unset;
                            margin-left: 10px;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .according-menu {
          right: unset;
          left: 20px;
        }
      }

    }

    &.only-body {
      .page-header {
        .header-wrapper {
          .nav-right {
            right: unset;
            left: 30px;
          }
        }
      }
    }
  }

  // task page css
  .date-details {
    >div {
      +div {
        margin-left: unset;
        margin-right: 10px;
      }
    }
  }

  // contact page css
  .contact-editform {
    &.ps-0 {
      padding-left: 20px !important;
    }
  }

  .contact-options {
    li {
      .btn-category {
        padding-left: unset !important;
        padding-right: 40px;
      }
    }
  }

  .list-persons {
    .profile-mail {
      .email-general {
        ul {
          padding-right: unset;
          padding-left: 20px;

          li {
            >span {
              float: left;
            }
          }
        }
      }

      .media {
        .media-body {
          ul {
            li {
              +li {     
                padding-right: unset;
                padding-left: 10px;
                margin-right: unset;
                margin-left: 5px;
                border-right: unset;
                border-left: 1px solid $light-color;
              }
            }
          }
        }
      }
    }
  }

  // file manager css
  .file-content {
    .form-inline {
      i {
        padding-right: unset;
        padding-left: 10px;
      }
    }
  }

  .file-sidebar {
    .btn {
      svg {
        margin-right: unset;
        margin-left: 8px;
      }
    } 
    .pricing-plan{
      .bg-img{
        left: -40px;
        right: unset;
      }
    } 
  }

  // projects page css
  .form-select {
    border-radius: 0px;
    background-position: left 0.75rem center;
  }

  .customers {
    ul {
      li {
        +li {
          margin-left: unset;
          margin-right: -10%;
        }
      }
    }
  }

  .project-list {
    .border-tab {
      &.nav-tabs {
        .nav-item {
          .nav-link {
            padding: 5px 0 5px 30px;
          }
        }
      }
    }
  }

  .project-box {
    .badge {
      right: unset;
      left: 30px;
    }
  }

  // kanban board css
  .kanban-board {
    float: right;
  }

  .kanban-item {
    .kanban-box {
      .customers {
        ul {
          li {
            +li {
              margin-left: unset;
              margin-right: -10px;
            }
          }
        }
      }
    }

    .list {
      li {
        i {
          margin-right: unset;
          margin-left: 10px;
        }
      }
    }
  }

  //horizontal-sidebar
  .page-wrapper {
    &.horizontal-wrapper {
      .header-logo-wrapper {
        text-align: right;
      }

      .page-body-wrapper {
        .page-body {
          margin-right: 0;
        }

        footer {
          margin-right: 0;
        }
      }
    }

    &.material-type {
      .page-body-wrapper {
        .page-body {
          margin-right: auto;
        }
      }
    }
  }

  .page-header {
    .header-wrapper {
      .nav-right {
        >ul {
          >li {
            &:first-child {
              margin-right: 0;
            }
          }
        }

        &.right-header {
          ul {
            li {
              .profile-media {
                .media-body {
                  margin-right: 15px;
                  margin-left: unset;
                }
              }
            }
          }
        }
      }

      .toggle-sidebar {
        margin-left: 2px;
        margin-right: unset;
      }
    }
  }

  // compact icon css
  .page-wrapper {
    .page-header {
      .header-wrapper {
        .nav-right {
          .notification-dropdown {
            left: -20px !important;
            right: unset !important;

            @media (max-width: 575.98px) {
              left: 12px !important;
            }
          }
        }

        .search-full {
          input {
            padding-left: unset;
            padding-right: 60px;
            text-align: right;
          }

          .form-group {
            &:before {
              left: unset;
              right: 30px;
            }

            .close-search {
              right: unset;
              left: 0;
            }
          }
        }
      }
    }

    &.compact-sidebar {
      .toggle-sidebar {
        margin-left: 15px;
        margin-right: unset;
      }

      .page-header {
        margin-left: unset;
        margin-right: 150px;

        &.close_icon {
          margin-right: 0;
        }
      }

      .page-body-wrapper {
        .page-body {
          margin-left: unset;
          margin-right: 150px;
        }

        .footer {
          margin-left: unset;
          margin-right: 150px;
        }

        div.sidebar-wrapper {
          >div {
            text-align: right;
          }

          .sidebar-main {

            .sidebar-links {
              li {
                a {
                  &.active {

                    ~.sidebar-submenu,
                    ~.mega-menu-container {
                      animation: fadeInRight 1000ms ease-in-out;
                    }
                  }
                }

                .sidebar-submenu,
                .mega-menu-container {
                  right: 150px;
                  left: unset;

                  li {
                    a {
                      padding: 15px 40px 15px 15px;

                      .according-menu {
                        right: 20px;
                      }

                    }
                  }

                  >li {
                    >a {
                      &::after {
                        left: unset;
                        right: 20px;
                      }
                    }
                  }

                  .mega-box {

                    .link-section {
                      text-align: right;

                      .submenu-title {
                        h5 {
                          padding: 15px 40px 15px 15px;

                          &::after {
                            left: unset;
                            right: 20px;
                          }
                        }
                      }
                    }
                  }

                }

              }

              .sidebar-list {

                .badge {
                  left: unset;
                  right: 53%;
                }

                .sidebar-link {
                  text-align: center;
                }
              }

              .mega-menu {
                text-align: center;

                .according-menu {
                  right: unset;
                  left: 20px;
                }
              }
            }
          }

          &.close_icon {
            transform: translateX(150px);

            ~.page-body,
            ~.footer {
              margin-right: 0px;
            }
          }
        }
      }
    }

    &.compact-small {
      .page-header {
        margin-right: 90px;
      }

      .page-body-wrapper {
        div.sidebar-wrapper {
          .sidebar-main {
            .sidebar-links {
              li {

                .sidebar-submenu,
                .mega-menu-container {
                  right: 90px;
                }
              }
            }
          }
        }

        .footer,
        .page-body {
          margin-right: 90px;
        }
      }
    }

    &.material-icon {
      .page-header {
        padding-left: unset;
        padding-right: 90px;
        margin: 0 auto;

        &.close_icon {
          padding-right: 0;
        }
      }

      .page-body-wrapper {
        .page-body {
          padding-left: 15px;
          padding-right: 90px;
          margin: 0 auto;
        }

        div.sidebar-wrapper {
          &.close_icon {
            ~.page-body {
              padding-right: 15px;
              margin: 0 auto !important;
            }
          }
        }
      }
    }
  }

  .apexcharts-canvas {
    direction: ltr;
  }

  // bookmark app css
  .email-wrap {
    .btn-mail {
      text-align: right !important;
    }

    .email-app-sidebar {
      ul {
        li { 
          a {
            padding-left: unset;
            padding-right: 20px;
          }
        }
      }
    }
  }

  .bookmark-tabcontent {
    .details-bookmark {
      .bookmark-card {
        &.card {
          .favourite-icon {
            text-align: center;
          }

          .title-bookmark {
            text-align: right;
          }
        }
      }
    }
  }
  .bookmark-tabcontent {
  	.details-bookmark.list-bookmark {
  		.bookmark-card {
  			.details-website {
  				.title-bookmark {
  					.hover-block {
  						left: 30px;
              right: unset;
  						text-align: left;
  					}
  				}
  			}
  		}
  	}
  }
  .bookmark-wrap {
    .details-bookmark {
      .row {
        >div {
          +div {
            padding-left: 15px !important;
          }
        }
      }
    }
  }

  // index page css start
  .appointment-table {
    table {
      tr {
        td {
          .status-circle {
            left: unset;
            right: 35px;
          }
        }
      }
    }
  }

  .notification {
    .card {
      .d-flex {
        .w-100 {
          p {
            .badge {
              margin-left: unset;
              margin-right: 10px;
            }
          }
        }
      }
    }
  }

  // index page css end
  @each $progress-gradient-name,
  $progress-gradient-color in (primary, var(--theme-default)),
  (secondary, $secondary-color),
  (success, $success-color),
  (danger, $danger-color),
  (info, $info-color),
  (light, $light-color),
  (dark, $dark-color),
  (warning, $warning-color) {
    .progress-gradient-#{$progress-gradient-name} {
      background-image: linear-gradient(to right, $progress-gradient-color, $transparent-color);

      &::after {
        right: unset;
        left: -1px;
      }

      .animate-circle {
        right: unset;
        left: -8px;
      }
    }
  }

  .progress-animate {
    span {
      left: -10px;
      right: unset;
    }
  }

  // ecommerce dashboard css end
  .me-4 {
    margin-left: 1.5rem !important;
    margin-right: unset !important;
  }

  .pe-4 {
    padding-left: 1.5rem !important;
    padding-right: unset !important;
  }

  .me-1 {
    margin-left: 0.25rem !important;
    margin-right: unset !important;
  }

  .navs-dropdown {
    .onhover-show-div {
      right: 0;
    }
  }

  .default-according {
    &.style-1 {
      button {
        text-align: right;

        &:before {
          left: 20px;
          right: unset;

          @media (max-width: 575px) {
            left: 15px;
          }
        }

        i {
          right: 18px;
          left: unset;
        }
      }
    }

    .card {
      .card-header {
        i {
          right: 18px;
          left: unset;
        }
      }
    }
  }

  .faq-wrap {
    .faq-accordion {
      .card {
        .card-header {
          .btn-link {
            padding-right: 20px !important;

            @media (max-width: 575.98px) {
              padding-right: 10px !important;
            }
          }
        }
      }
    }
  }

  .ProfileCard-details {
    padding-right: 30px;
    padding-left: unset;
    float: right;
  }

  .starter-main {
    .card-body {
      ul {
        padding-right: 30px;
        padding-left: unset;
      }
    }
  }

  .theme-tab {
    .tab-title {
      li {
        a {
          svg {
            margin-left: 5px;
            margin-right: unset;
          }
        }
      }
    }
  }

  .spent {
    .spent-graph {
      .project-budget {
        margin-left: 30px;
        margin-right: unset;
      }
    }
  }

  .support-table {
    table {
      tbody {
        tr {
          td {
            &:first-child {
              padding-right: 0;
              padding-left: unset;
            }
          }
        }
      }
    }
  }

  .order-box {
    .sub-total,
    .total,
    .qty {

      .shipping-class,
      li {

        .shopping-checkout-option,
        span {
          float: left;
        }
      }

      li {
        .count {
          float: left;
        }
      }
